.contact {
    padding: 4.4rem 0 7.5rem;
}

.contact_title {
    font-size: 4.5rem;
    color: $black;
    font-weight: 400;

    text-align: center;

    margin-bottom: 3.6rem;
}

.contact__form {
    display: flex;
    flex-direction: column;

    padding: 0 25.5rem;
}

.contact__input {
    font-family: $oswald;
    font-size: 2.4rem;
    color: $black;
    font-weight: 400;
    background: none;
    border: none;
    border-bottom: 1px solid rgba($black, 0.1);


    padding: 2.2rem;
    margin-bottom: 2rem;

    &:focus {
        outline: none;
    }

    &::placeholder {
        color: rgba($black, 0.5);
        text-transform: uppercase;
    }

}

.contact__checkbox {
    font-size: 1.7rem;
    color: #454545;
    font-weight: 400;
    text-align: center;

    input {
        margin-right: 1rem;
    }

    margin-bottom: 2.2rem;
}

.contact__btn {
    text-align: center;
}