.btn {
    display: inline-block;
    width: 100%;
    font-size: 1.4rem;
    color: $white;
    font-weight: 400;
    text-decoration: none;
    background: $green;
    padding: 1rem 0;
    text-align: center;
    border-radius: 100px;

    // &--yellow {
    //     background: $yellow;
    //     color: $black;
    // }

    &--white {
        transition: background 0.1s ease-in-out;
        background: $white;
        color: $black;
    }

    &--more {
        display: inline-block;
        width: auto;
        font-size: 1.8rem;
        color: $black;
        background: #EDEDED;

        padding: 1.4rem 12.3rem;
    }

    &--contact {
        width: auto;
        padding: 1.3rem 7.6rem;
    }
}