.location {
    padding: 4.4rem 0 17rem;

    position: relative;

    &:after {
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: 50%;
        z-index: 1;
        width: 4px;
        height: 60px;
        background: #E5E5E5;
    }
}

.location__title {
    font-size: 4.2rem;
    color: $black;
    font-weight: 400;

    text-align: center;
    text-transform: uppercase;

    margin-bottom: 5rem;
}

.location__inner {
    display: flex;
}

.location__map {
    width: 55%;
    cursor: pointer;

    img {
        width: 100%;
    }
}

.location__contacts {
    width: 45%;
    background: #EDEDED;
    padding: 7.6rem 6.9rem 0;

    margin-left: 2rem;
}

.location__name {
    font-family: $oswald;
    font-size: 2.4rem;
    color: $black;

    margin-bottom: 3rem;
}

.location__address {
    font-style: normal;
    font-size: 1.8rem;
    color: $green;
    font-weight: 400;
    text-decoration: underline;

    margin-bottom: 3rem;
}

.location__info {
    font-size: 2.4rem;
    color: $black;
    font-weight: 400;

    margin-bottom: 3rem;
}

.location__number {
    font-size: 1.8rem;
    color: $black;
    font-weight: 400;

    a {
        color: inherit;
        text-decoration: none;
        font-size: 2.4rem;
    }

    margin-bottom: 0.5rem;
}

.location__email {
    font-size: 1.8rem;
    color: $black;
    font-weight: 400;

    a {
        color: inherit;
    }
}